import {apiGet, apiPut, apiDelete, apiPost} from '../../assets/js/baseApi'

let getRepoBSPConfigApi = function(bsprooturl, boardname){
    return new Promise((resolve, reject) => {
        let data = {
            bsprooturl,
            boardname,
            type: "sysbsp"
        }
        apiGet("/api/deploymgr/package", data).then((data) =>{
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

let getBSPUpdateStatus = function(){
    return new Promise((resolve, reject) => {
        let data = {
            type: "sysbsp"
        }
        apiGet("/api/deploymgr/status", data).then((data) => {
            resolve(data)
        }).catch((err) => {
            resolve(err.response)
        })
    })
}

