import {repoApiGet, repoApiPost} from '@/assets/js/baseApi'
import {getRepoServerApi} from "../restfulapi/serverConfigApi"
import {getToken} from "./getRepoTokenApi"

let getRepoBspsApi = function(boardname){
    return new Promise((resolve, reject) => {
       
        getRepoServerApi().then((repoUrl) => {
            let getRepoBspUrl =  repoUrl + "/repobsps/boardname/"+boardname;
            repoApiGet(getRepoBspUrl, "").then((data) => {
                resolve(data);
            }).catch((error) => {
                resolve(error.response);
            })
        });
        
    })
}

let getRepoBspsByOsApi = function(os){
    return new Promise((resolve, reject) => {
        getRepoServerApi().then((repoUrl) => {
            let getRepoBspUrl =  repoUrl + "/repobsps/os/"+os;
            repoApiGet(getRepoBspUrl, "").then((data) => {
                resolve(data);
            }).catch((error) => {
                resolve(error.response);
            })
        });
    })
}

export {
    getRepoBspsApi,
    getRepoBspsByOsApi
}


