<template>
    <div v-show="isSingleMode">
        <el-tabs v-model="activeName"  @tab-click="handleClick">
            <el-tab-pane name="bspMarket">
                <span slot="label">
                    <i class="fa fa-sellsy m-r-5" aria-hidden="true"></i>
                    {{$t('systemUpdate.systemMarket')}}
                </span>
                <el-table
                    :data="bspTableData"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="bspLoading"
                    >
                    <el-table-column
                    label=""
                    width="70"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img src="@/assets/imgs/bspUpdatePkg.png" alt="no img" style="width:38px">
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                        prop="boardname"
                        :label="$t('systemUpdate.boardName')"
                        min-width="100"
                        >
                    </el-table-column>  
                    <el-table-column
                        prop="versionname"
                        :label="$t('systemUpdate.buildVersion')"
                        min-width="120"
                        >
                    </el-table-column> 
                    <el-table-column
                        prop="description"
                        :label="$t('systemUpdate.description')"
                        min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    :label="$t('systemUpdate.action')"
                    min-width="120">
                        <template slot-scope="scope">
                            <div>  
                                <el-button 
                                size="small" 
                                type="success" 
                                @click="selectBSP(scope.row)" 
                                >
                                    {{$t('systemUpdate.deploy')}}
                                </el-button> 
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <el-tab-pane name="bspInfo">
                <span slot="label">
                    <i class="fa fa-info m-r-5" aria-hidden="true"></i>
                    {{$t('systemUpdate.systemInfo')}}
                </span>
                <el-form  label-width="150px" class="m-t-20 m-l-30" size="mini">
                    <el-form-item :label="$t('label.systemVersion')">
                        <el-input v-model="systemVersion" class="max-w-300 m-t-5" disabled></el-input>
                    </el-form-item>
                    <el-form-item :label="$t('label.boardName')" >
                        <el-input v-model="boardName" class="max-w-300 m-t-5" disabled></el-input>
                    </el-form-item>
                    <el-form-item :label="$t('label.platformName')">
                        <el-input v-model="platform" class="max-w-300 m-t-5" disabled></el-input>
                    </el-form-item>
                    <el-form-item :label="$t('label.buildVersion')">
                        <el-input v-model="bspVersion" class="max-w-300 m-t-5" disabled></el-input>
                    </el-form-item>
                </el-form>
            </el-tab-pane>

            <el-tab-pane name="bspUpdateHistory">
                <span slot="label"><i class="fa fa-history m-r-5" aria-hidden="true"></i>
                    {{$t('systemUpdate.systemUpdateHistory')}} 
                </span>
                <el-table
                    :data="bspHistoryTableList"
                    tooltip-effect="dark"
                    style="width: 100%"
                    @selection-change="selectItem"
                    @filter-change="filterStatus"
                    :default-sort = "{prop: 'ts', order: 'descending'}"
                    ref="bspHistoryTable"
                    v-loading="listLoading" 
                    >
                     <el-table-column
                    type="selection"
                    min-width="50"
                    :selectable="checkSelectable">
                    </el-table-column>
                    <el-table-column
                    prop="devname"
                    :label="$t('systemUpdate.deviceName')"
                    min-width="120"
                    >
                    </el-table-column>

                    <el-table-column
                    prop="deployname"
                    :label="$t('systemUpdate.deployName')"
                    min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="comment"
                    :label="$t('systemUpdate.deployComment')"
                    min-width="140"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="boardname"
                    :label="$t('systemUpdate.boardName')"
                    min-width="120"
                    >
                    </el-table-column>

                    <el-table-column
                    prop="version"
                    :label="$t('systemUpdate.buildVersion')"
                    min-width="140"
                    >
                    </el-table-column>
                    
                    <el-table-column
                    :label="$t('systemUpdate.beginTime')"
                    min-width="120"
                    prop="ts"
                        >
                        <template slot-scope="scope">
                            {{scope.row.ts|time}}
                        </template>
                    </el-table-column>

                    <el-table-column
                    :label="$t('systemUpdate.finishTime')"
                    min-width="130"
                    prop="tsEnd"
                        >
                        <template slot-scope="scope">
                            {{scope.row.tsEnd|time}}
                        </template>
                    </el-table-column>

                    <el-table-column
                    column-key="updateStatus"
                    prop="updstatus"
                    :label="$t('systemUpdate.updateStatus')"
                    min-width="140"
                    :filters="[{ text: $t('global.success'), value: 2 }, { text: $t('global.failed'), value: 1 },{text: $t('global.deploying'), value: 0}]"
                    filter-placement="bottom-end"
                    >
                        <template slot-scope="scope">
                            <img src="@/assets/imgs/loading1.gif" alt="" style="width:50px;height:10px;"v-if="scope.row.updstatus==0">
                            <span v-if="scope.row.updstatus==2" class="badge bg-green">{{$t('global.success')}}</span>
                            <span v-if="scope.row.updstatus==1" class="badge bg-DimGray">{{$t('global.failed')}}</span>
                            <!-- {{scope.row.upstatus|BSPUpdateStatus}} -->
                        </template>
                    </el-table-column>

                    <el-table-column
                    :label="$t('systemUpdate.errorStatus')"
                    width="120"
                    >
                        <template slot-scope="scope">
                            {{""+scope.row.errorcode|BSPErrorStatus}}
                        </template>
                    </el-table-column>
                     <el-table-column
                    :label="$t('systemUpdate.action')"
                    min-width="175"
                    align="center">
                        <template slot-scope="scope">
                            <div>
                                <el-button-group>
                                    <el-button 
                                        size="mini" 
                                        type="danger"
                                        @click="deleteHistory(scope.row.dusid)"
                                        :disabled="scope.row.updstatus==0"
                                        >
                                            {{$t('global.delete')}}
                                    </el-button>
                                    <el-button 
                                        size="mini" 
                                        type="primary"
                                        @click="redeploy(scope.row)"
                                        :disabled="scope.row.updstatus!=1"
                                        >
                                            {{$t('systemUpdate.redeploy')}}
                                    </el-button>
                                </el-button-group>
                            </div>
                        </template>
                    </el-table-column>

                </el-table>
                <btn-group 
                :selectedData="multipleSelection" 
                :isDelete="true" 
                :isMove="false" 
                tid="dusid" 
                :delApi="batchDeleteBSPUpdateHistoryApi" 
                @result="isSuccess" 
                v-show="isShowBtnGroup"
                class="fl m-t-10 cf">
                </btn-group>
                <el-button 
                v-loading="clearLoading" 
                size="small" 
                @click="clearAll()" 
                type="primary" 
                class="fl m-t-10 cf m-l-10"
                v-show="isShowBtnGroup">
                {{$t('global.clearAll')}}
                </el-button>
                <div class="m-t-10 cf fr">
                    <el-pagination
                    @current-change="handleCurrentChange"
                    layout="prev, pager, next"
                    :page-size="limit"
                    :current-page="currentPage"
                    :total="dataCount"
                    v-show="isshow"
                    background
                    >
                    </el-pagination>
                </div> 
            </el-tab-pane>
        </el-tabs>
        <el-dialog :visible.sync="dialogDeployNameVisible" :close-on-click-modal="false">
            <div slot="title" class="dialog-title">
                <i class="fa fa-level-up m-r-10" ></i>
                {{$t('systemUpdate.systemDeploy')}}
            </div>
            <el-form :model="bspUpdateForm" :rules="rules" ref="bspUpdateForm">
                <el-form-item :label="$t('label.deployName')" label-width="230px" prop="deployName">
                    <el-input v-model.trim="bspUpdateForm.deployName" autocomplete="off" class="max-w-350" :placeholder="$t('systemUpdate.updateSystemDeployName')"></el-input>
                </el-form-item>
                <el-form-item :label="$t('label.deployComment')" label-width="230px" prop="deployComment">
                    <el-input 
                    v-model.trim="bspUpdateForm.deployComment" 
                    autocomplete="off" class="max-w-350" 
                    type="textarea" 
                    :autosize="{ minRows: 1, maxRows: 3}"
                    :placeholder="$t('systemUpdate.updateSystemComment')">
                    </el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogDeployNameVisible = false">{{$t("global.cancel")}}</el-button>
                <el-button type="primary" @click="execBSPUpdate('updateBSP')">{{$t("global.confirm")}}</el-button>
            </div>
        </el-dialog>
    </div> 
</template>

<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .badge{
        display: inline-block;
        min-width: 10px;
        padding: 5px 8px;
        font-size: 12px;
        font-weight: 700;
        line-height: 1;
        color: #fff;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        // background-color: #777;
        border-radius: 10px;
    }
    .el-loading-mask{
        top: 60px;
    }

</style>

<script>

    import {bspUpdate} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus, reExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import {androidControl, bspInfoWrite, actionDevice} from '@/assets/js/lwm2mMap';
    import {getRepoBspsApi, getRepoBspsByOsApi} from "../../restfulapi/getRepoBspApi"
    import {getRepoBSPConfigApi, 
    getBSPUpdateStatus, 
    saveComment, 
    deleteBSPUpdateHistoryApi, 
    deleteBSPUpdateHistoryAllApi,
    batchDeleteBSPUpdateHistoryApi,
    getBSPUpdateStatusByDevIdApi,
    getBSPUpdateStatusByPageAndDevIdApi,
    getBSPUpdateStatusByPageAndStatusAndDevIdApi,
    deleteBSPUpdateHistoryAllByDevIdApi} from '@/components/restfulapi/updateBSPApi'
    import { setInterval, clearInterval } from 'timers';
    import btnGroup from "../../../common/btn-group";
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage";
    import {groupType} from "../../../assets/js/constant"


    export default{
        name: 'controlSystemUpdate',
        data(){
            let validateDepName = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            let validateDepComment = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,48}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyDescriptionError')))
                }else{callback()};
            };
            return {
                dialogDeployNameVisible: false,
                activeName: "bspMarket",
                systemVersion: '',
                boardName: '',
                bspVersion: '',
                platform: '',
                bspTableData: [],
                bspHistoryTableList: [], 
                isshow: false,
                limit: 10,
                dataCount: 0,
                currentPage: 1,
                multipleSelection: [],
                isShowBtnGroup: false,
                batchDeleteBSPUpdateHistoryApi: batchDeleteBSPUpdateHistoryApi,
                bspUpdateForm: {
                    bspDownloadPath: '',
                    deployComment: '',
                    deployName: '',
                    updateBSPVersion: '',
                },
                rules:{
                    deployName: {required: true, validator: validateDepName, trigger: 'blur'},
                    deployComment: {required: true, validator: validateDepComment, trigger: 'blur'},
                },

                intervalTime: 10000,
                clearLoading: false,
                listLoading: false,
                bspLoading: false,
                isInterval: false,

                statusArray: []
            }
        },
        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },

            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            },

            deviceType: {
                type: String,
                default: ""
            }
        },

        components: {
            btnGroup
        },

        methods:{
             //batch selected
            selectItem(val) {
                this.multipleSelection = val;
            },

            checkSelectable(row) {
                return !row.updstatus==0;
            },
            
            getDeviceBspInfo(){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(this.selectedAgentId == ""){
                    console.error("selectedAgentId is empty");
                    return;
                }
                let i=0
                let bsplength = Object.keys(bspUpdate).length;
                for(let key in bspUpdate){
                    this.bspLoading = true;
                    getDeviceStatus(this.selectedAgentId, bspUpdate[key]).then((data) => {
                        this.bspLoading = false;
                        handleResponse(data, (res) => {
                            if(res.status === "CONTENT"){
                                this[key] = res.content.value;
                                i++;
                                if(i == bsplength){
                                    this.getRepoBSP();
                                }
                            }
                            
                        })
                    })
                }
            },
            //get repo bsp
            getRepoBSP(){
                this.bspLoading = true; 
                if(this.deviceType == groupType.android){
                    getRepoBspsApi(this.boardName).then((data) => {
                        this.bspLoading = false;
                        handleResponse(data, (res) => {
                            if(res.status === "success"){
                                if(_g.isArray(res.data)){
                                    this.bspTableData = res.data;
                                }
                            }

                        })
                        
                    })
                }else{
                    getRepoBspsByOsApi(this.deviceType).then((data) => {
                        this.bspLoading = false;
                        handleResponse(data, (res) => {
                            if(res.status === "success"){
                                if(_g.isArray(res.data)){
                                    this.bspTableData = res.data;
                                }
                            }

                        })
                        
                    })
                }
                
            },
            compareBSPVersion(repoBSPVersion, clientBSPVersion){
                let repoBSPVersionArray = repoBSPVersion.split(".");
                let clientBSPVersionArray = clientBSPVersion.split(".");
                if(repoBSPVersionArray[0] === clientBSPVersionArray[0] && repoBSPVersionArray[1] === clientBSPVersionArray[1]){
                    if(parseInt(repoBSPVersionArray[2]) > parseInt(clientBSPVersionArray[2])){
                        return true
                    }else if(parseInt(repoBSPVersionArray[2]) == parseInt(clientBSPVersionArray[2])){
                        if(parseInt(repoBSPVersionArray[3]) > parseInt(clientBSPVersionArray[3])){
                            return true;
                        }
                    }
                } 
                return false;
            },

            singleBSPUpdate(cid,  agentId){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(actionDevice[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }

                let dpData = {
                    dpname: this.bspUpdateForm.deployName,
                    dpcomment: this.bspUpdateForm.deployComment,
                    version: this.bspUpdateForm.updateBSPVersion,
		            boardName: this.boardName,
                    bspimagepath: this.bspUpdateForm.bspDownloadPath
                };
                execDeviceStatus(agentId, actionDevice[cid], dpData).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CHANGED"){
                            this.activeName = "bspUpdateHistory";
                            this.intervalGetUpdateBSPStatus()
                        }else{
                            _g.handleError(res)
                        }
                    })
                })
            },

            reBSPUpdate(cid,  agentId){
                if(!this.isSingleMode) {
                    console.error("Cannot operate in a batch mode");
                    return;
                }
                if(actionDevice[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }

                let dpData = {
                    dpname: this.bspUpdateForm.deployName,
                    dpcomment: this.bspUpdateForm.deployComment,
                    version: this.bspUpdateForm.updateBSPVersion,
                    boardName: this.boardName,
                    bspimagepath: this.bspUpdateForm.bspDownloadPath
                };
		this.listLoading = true;
                reExecDeviceStatus(agentId, actionDevice[cid], dpData).then((data) => {
		    this.listLoading = false
                    handleResponse(data, (res) => {
                        if(res.status === "CHANGED"){
                            this.activeName = "bspUpdateHistory";
                            this.currentPage = 1;
                            this.intervalGetUpdateBSPStatus()
                        }else{
                            _g.handleError(res)
                        }
                    })
                })
                
            },

            execBSPUpdate(cid){
                if(this.selectedAgentId === ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }
               
               this.$refs.bspUpdateForm.validate((valid) => {
                    if (valid) {
                        this.singleBSPUpdate(cid, this.selectedAgentId);
                        this.dialogDeployNameVisible = false;
                    }
               })
          
            },

            redeploy(item){
                 _g.swalInfoDo(this.$t('global.areYouSure'),this.$t('systemUpdate.redeploy'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        this.bspUpdateForm.deployComment = item.comment;
                        this.bspUpdateForm.deployName = item.deployname;
                        this.bspUpdateForm.updateBSPVersion = item.version;
                        this.bspUpdateForm.bspDownloadPath = item.address;
                        this.boardName  = item.boardname;
                        this.reBSPUpdate('updateBSP', item.deviceId);
                    }
                }) 
            },

            checkUpStatusData(){
                getBSPUpdateStatusByDevIdApi(this.selectedAgentId);
            },

            getUpdateBSPStatus(){
                if(!this.selectedAgentId){
                    console.error("[getUpdateBSPStatus]selectedAgentId is empty");
                    return;
                }
                 if(this.isInterval === false){
                    this.listLoading = true;
                }
                if(this.statusArray.length > 0){
                    getBSPUpdateStatusByPageAndStatusAndDevIdApi(this.selectedAgentId, this.currentPage, this.limit, this.statusArray).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            this.assignUpStatus(res)
                        })
                    })
                }else{
                    getBSPUpdateStatusByPageAndDevIdApi(this.selectedAgentId, this.currentPage, this.limit).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            this.assignUpStatus(res)
                        })
                    })
                }

            },
            assignUpStatus(res){
                if(Object.prototype.toString.call(res)  === '[object Object]'){
                    this.dataCount = res.count;
                    this.bspHistoryTableList = res.data;
                    this.isshow = this.dataCount > this.limit;
                    this.isShowBtnGroup = this.dataCount > 0;
                    let isFinish = true;
                    res.data.forEach((val) => {
                        if(val.updstatus == 0){
                            isFinish = false;
                        }
                    })
                    if(isFinish){
                        window.clearInterval(_g.timer);
                        _g.timer = null;
                    }
                }else{
                    window.clearInterval(_g.timer);
                    _g.timer = null;
                }
            },

            filterStatus(value){
                this.statusArray = value.updateStatus;
                this.currentPage = 1;
                if(_g.isArray(this.statusArray)){
                   this.intervalGetUpdateBSPStatus();
                }
            },
            selectBSP(bspInfo){
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')})
                    return;
                }
                this.bspUpdateForm.deployName = "";
                this.bspUpdateForm.deployComment = "";
                this.bspUpdateForm.updateBSPVersion = bspInfo.versionname;
                this.bspUpdateForm.bspDownloadPath = bspInfo.address;
                this.dialogDeployNameVisible = true;
            },  
            initData(){
                this.systemVersion= '';
                this.boardName= '';
                this.bspVersion= '';
                this.bspTableData= [];
                this.bspHistoryTableList = [];
                this.platform= "";
                this.currentPage = 1;
                Object.assign(this.$data.bspUpdateForm, this.$options.data().bspUpdateForm)
            },

            handleCurrentChange(currentPage){
                if(currentPage){
                    this.currentPage = currentPage;
                }else{
                    this.currentPage = 1
                }
                this.intervalGetUpdateBSPStatus();
            },
            
            deleteHistory(dusid){
                 _g.swalInfoDo(this.$t('global.areYouSure'),this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                        deleteBSPUpdateHistoryApi(dusid).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetUpdateBSPStatus();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }
                 })
                
            },

            isSuccess(result){
                if(result){
                    this.intervalGetUpdateBSPStatus();
                }
            },

            intervalGetUpdateBSPStatus(){
                this.getUpdateBSPStatus();
                if(_g.timer === null){
                    _g.timer = window.setInterval(() => {
                        this.getUpdateBSPStatus();
                        this.isInterval = true;
                    },this.intervalTime)
                }
            },
            
             //clear All history
            clearAll(){
                _g.swalInfoDo(this.$t('global.areYouSure'),this.$t('global.clearAll'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                    if(result){
                         this.clearLoading = true;
                        deleteBSPUpdateHistoryAllByDevIdApi(this.selectedAgentId).then((data) => {
                            this.clearLoading = false;
                            handleResponse((data), (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetUpdateBSPStatus();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }
                })
                
            },

            handleClick(tab){
                if(tab.name === "bspUpdateHistory"){
                    this.$emit("isDisabled", false)
                }else{
                    this.$emit("isDisabled", true)
                }
            },
        },

        beforeDestroy() {
            window.clearInterval(_g.timer);
            _g.timer = null;
        },
        
        watch: {
            isSingleMode(val){
                if(val){
                    this.initData();
                }
            },
            selectedAgentId(val){
                if(this.isSingleMode) {
                    this.initData();
                    if(val){
                        if(!this.isOffline){
                             this.getDeviceBspInfo();
                        }
                        this.checkUpStatusData();
                        this.intervalGetUpdateBSPStatus();
                    }

                }
                
            },
        }

    }
</script>





